.footer_outer_container {
  background-color: #141414;
  color: #999;
  padding: 40px 20px;
  text-align: center;
}

.footer_inner_container {
  max-width: 1000px;
  margin: 0 auto;
}

.footer_icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.footer_icons svg {
  font-size: 28px;
  cursor: pointer;
  transition: color 0.3s;
}

.footer_icons svg:hover {
  color: #e50914;
}

.footer_links {
  display: flex;
  justify-content: center;
  gap: 50px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.footer_links ul {
  list-style: none;
  padding: 0;
}

.footer_links li {
  margin: 8px 0;
  cursor: pointer;
  transition: color 0.3s;
}

.footer_links li:hover {
  color: #fff;
}

.footer_service {
  margin: 20px 0;
}

.footer_service button {
  background: transparent;
  border: 1px solid #999;
  padding: 8px 16px;
  color: #999;
  cursor: pointer;
  transition: all 0.3s;
}

.footer_service button:hover {
  border-color: #fff;
  color: #fff;
}

.footer_copy {
  font-size: 14px;
  color: #666;
  margin-top: 20px;
}
@media (max-width: 768px) {
  .footer_links {
    gap: 30px;
  }

  .footer_icons svg {
    font-size: 24px;
  }
}

@media (max-width: 480px) {
  .footer_links {
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }

  .footer_icons {
    gap: 15px;
  }

  .footer_icons svg {
    font-size: 20px;
  }

  .footer_copy {
    font-size: 12px;
  }
}
